


























































































import uniLoadMore from '@/components/uni-load-more.vue';
import uniCountdown from '../../components/uni-countdown/uni-countdown.vue';
import {
	mapState,
	mapMutations,
	
} from 'vuex';
export default {
computed: mapState([ 'hasLogin','userInfo']),
components: {
	uniCountdown,uniLoadMore,
},
	data(){
		return {
			nowtime:0,
			isactive: 0,	
			scrollLeft: 0,
			task:[],
			/* 加载更多 */
			loadingType: 0,
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			isPosition:false,
			activeClass:'tabactive',
			falseClass:'tabactive-nop',	
			limit:2,
			/* 加载更多 */
			request:true,
		};
	},
	onLoad(){
		
		
		this.nowtime=parseInt(new Date().getTime()/1000) 
		 if(this.hasLogin){	
			uni.request({
				url:`${this.$serverUrl}/task/task`,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				success: (e) => {	
					
					this.task = e.data.order; 
				},
				fail: (e) => {
					console.log('data fail')
				}
			});
		}else{
			uni.navigateTo({
				url:"../login/login"
			})
		}			 
	},
	
	methods:{
		LoadMore(){
			if(this.loadingType !==2){
				this.loadingType = 1;
				if(this.request){
					this.request=false;
					setTimeout(() => {
						this.addData();
					}, 1200);
				}else{
					this.loadingType = 2;
				}
			}	
		},
		
		addData(){
			
			uni.request({
				url: 'http://t2.qingxinedu.cn/api/task/task?limit='+this.limit,
				method: 'GET',
				data: {limit:this.limit},
				success: (res) => {
					const loadData = res.data;
					if(loadData.order.length==0){
						this.loadingType=2;			
					}else{
						
						this.task =this.task.concat(loadData.order) 
						this.limit = this.limit+1;
						this.request = true;
					}
					
				    }
			    });
				
		},
		
		
		activefn(status){
			uni.request({
				url:`${this.$serverUrl}/task/task?status=`+status,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				success: (e) => {	
					this.task = e.data.order; 
				},
				fail: (e) => {
					console.log('data fail')
				}
			});
		},
		
		
		//下单流程
		To_order(id){
			uni.navigateTo({
				url:'order_process?id='+id,
			})
		},
		To_review(id){
			uni.navigateTo({
				url:'order_review?id='+id,
			})
		},
		scroll(e){	
			console.log(111)
				this.isPosition = true;
		},
		//订单取消
		timeup(id){
			uni.request({
				url:`${this.$serverUrl}/task/cancel?id=`+id,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				success: (e) => {	
					
				},
				fail: (e) => {
					
				}
			})
		},
	},
	
}
